<template>
	<div class="box" v-lazy:background="require('../assets/index_bg.png')">
		<div class="language">
			<div>
				<img v-lazy="require('../assets/language.png')"/>
				<img v-lazy="require('../assets/lang_change.png')"/>
				<div>
					<span>English</span>
					<span class="active">中文</span>
				</div>
			</div>
		</div>
		<img v-lazy="require('../assets/home_logo.png')"/>
		<div class="list">
			<div
			  v-for="(item, index) in list"
			  :key="index"
			  :style="{ backgroundColor: (index + 1) % 2 == 0 ? '#1B2C42' : '#224360' }"
			>
				<span>{{ item.title }}</span>
				<span>{{ item.amount }}</span>
			</div>
		</div>
		<router-link to="home">进入</router-link>
	</div>
</template>

<script>
	import { Toast } from 'vant'
	
	export default {
		data() {
			return {
				list: [
					{ title: '矿池合约UPC', amount: '0.00' },
					{ title: '今日发行UPC', amount: '0.00' },
					{ title: '持有算力', amount: '0.00' },
					{ title: '传播算力', amount: '0.00' },
					{ title: '全网地址', amount: '0.00' },
					{ title: '流通量UPC', amount: '0.00' }
					// { title: '流通市值 $', amount: '0.00' }
				]
			}
		},
		methods: {
			init() {
				if (this.$cache.index) {
					this.list[0].amount = this.$cache.index.pool
					this.list[1].amount = this.$cache.index.today
					this.list[2].amount = this.$cache.index.hold
					this.list[3].amount = this.$cache.index.spread
					this.list[4].amount = this.$cache.index.address
					this.list[5].amount = this.$cache.index.relased
				} else {
					this.$cache.index = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.depositToken.methods.RelaseInfomations()
				.call((err, res) => {
					if (!err) {
						this.list[0].amount = this.$fromWei(res.poolBalance, 2)
						this.list[1].amount = this.$fromWei(res.todayRelase, 2)
						this.list[2].amount = res.staticValue
						this.list[3].amount = res.dynamicValue
						this.list[4].amount = res.addresses
						this.list[5].amount = this.$fromWei(res.relasedAmount, 2)
						
						this.$cache.index.pool = this.list[0].amount
						this.$cache.index.today = this.list[1].amount
						this.$cache.index.hold = this.list[2].amount
						this.$cache.index.spread = this.list[3].amount
						this.$cache.index.address = this.list[4].amount
						this.$cache.index.relased = this.list[5].amount
					}
				})
			},
			addToken() {
				ethereum.sendAsync({
				    method: 'wallet_watchAsset',
				    params: {
				      "type":"ERC20",
				      "options":{
				        "address": this.$contract.protossToken._address,
				        "symbol": 'PRC',
				        "decimals": 18,
				        // "image": tokenImage,
				      },
				    },
				    // id: Math.round(Math.random() * 100000)
				    id: parseInt(new Date().getTime() / 1000)
				}, (err, res) => {
					if (res.result) {
						Toast.success('添加成功')
						localStorage.setItem('token', 'true')
					} else {
						Toast.fail('Your loss!')
					}
				})
			}
		},
		mounted() {
			if (!localStorage.getItem('token')) {
				this.addToken()
			}
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.list {
		width: 100%;
		font-size: 28px;
		&>div {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 16px 33px;
		}
	}
	.language {
		display: flex;
		justify-content: flex-end;
		&>div {
			width: 158px;
			height: 48px;
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			border-radius: 50px;
			background-color: #1B2C42;
			&>img:first-child {
				width: 31px;
				height: 31px;
				margin-right: 20px;
			}
			&>img:nth-of-type(2) {
				width: 35px;
				height: 19px;
			}
			&>div {
				position: absolute;
				left: 0;
				top: calc(100% + 6px);
				width: 100%;
				height: 100px;
				display: flex;
				align-items: center;
				flex-direction: column;
				background-color: #224360;
				&>span {
					display: inline-block;
					width: 100%;
					height: 50%;
					font-size: 28px;
					line-height: 50px;
				}
				&>.active {
					background-color: #114D5F;
				}
			}
		}
	}
	.box {
		padding: 18px 35px 0;
		background-size: 100% 100% !important;
		background-repeat: no-repeat !important;
		&>img {
			width: 166px;
			height: 184px;
			margin-top: 122px;
			margin-bottom: 140px;
		}
		&>a {
			display: inline-block;
			width: 460px;
			height: 90px;
			color: #fff;
			font-size: 40px;
			font-weight: 400;
			margin-top: 100px;
			line-height: 90px;
			border-radius: 50px;
			background-color: #24C197;
		}
	}
</style>
